@import "normalize.scss"
@import "reset.scss"

*
  box-sizing: border-box

@font-face
  font-family: "ManropeS"
  src: url(../fonts/Manrope-Regular.woff) format("woff")
  src: local(Manrope-Regular)
  src: local(Manrope Regular)
  font-weight: normal
  font-style: normal
  font-display: swap

@font-face
  font-family: "ManropeS"
  src: url(../fonts/Manrope-Medium.woff) format("woff")
  src: local(Manrope-Medium)
  src: local(Manrope Medium)
  font-weight: 500
  font-style: normal
  font-display: swap

@font-face
  font-family: "ManropeS"
  src: url(../fonts/Manrope-Bold.woff) format("woff")
  src: local(Manrope-Bold)
  src: local(Manrope Bold)
  font-weight: 700
  font-style: normal
  font-display: swap

@font-face
  font-family: "ManropeS"
  src: url(../fonts/Manrope-SemiBold.woff) format("woff")
  src: local(Manrope-SemiBold)
  src: local(Manrope SemiBold)
  font-weight: 600
  font-style: normal
  font-display: swap

@font-face
  font-family: "ManropeS"
  src: url(../fonts/Manrope-ExtraBold.woff) format("woff")
  src: local(Manrope-ExtraBold)
  src: local(Manrope ExtraBold)
  font-weight: 800
  font-style: normal
  font-display: swap

$primary-color: #31353B

body
  font-family: "ManropeS"
  color: $primary-color
  font-weight: 400
  line-height: 1.35

.wrapper
  min-height: 100%
  overflow: hidden
  display: flex
  flex-direction: column

.title
  font-weight: 600
  font-size: 40px
  line-height: 1.38

.title_center
  text-align: center

.page
  flex: 1 1 auto

[class*="_container"]
  max-width: 1170px
  margin: 0 auto

.menu__link.active
  text-decoration: underline

.header
  position: absolute
  top: 0
  left: 0
  width: 100%
  z-index: 50
  &__container
    min-height: 103px
    display: flex
    column-gap: 10px
    row-gap: 15px
    justify-content: space-between
    align-items: center
    padding-bottom: 10px
    padding-top: 10px
  &__logo
    text-decoration: none
    font-weight: 800
    font-size: 23px
    color: $primary-color

.menu
  &__list
    display: flex
    justify-content: space-between
    column-gap: 46px
    row-gap: 15px
  &__link
    text-decoration: none
    font-weight: 700
    font-size: 13px
    line-height: 1.3
    color: #000000
    &:hover
      text-decoration: underline

.main-block
  padding-bottom: 110px
  &__container
    padding-top: 183px
    position: relative
    &::after
      content: ""
      background: #bdccff
      position: absolute
      width: 200%
      height: 100%
      top: 0
      left: 68%

.body-main-block
  position: relative
  z-index: 4
  display: flex
  align-items: center
  margin-bottom: 14px
  &__content
    position: relative
    z-index: 3
    flex: 0 1 50%
  &__slider
    min-width: 0
    flex: 0 1 50%
    filter: drop-shadow(0px 100px 80px rgba(0, 0, 0, 0.03)) drop-shadow(0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0227778)) drop-shadow(0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0182222)) drop-shadow(0px 20px 13px rgba(0, 0, 0, 0.015)) drop-shadow(0px 8.14815px 6.51852px rgba(0, 0, 0, 0.0117778)) drop-shadow(0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00722222))
  &__arrows
    position: absolute
    bottom: -52px
    right: 0
    display: flex
    gap: 40px
    align-items: center
  &__arrow.swiper-button-prev,
  &__arrow.swiper-button-next
    position: static
    height: 20px
    width: 11px
    background: url('../img/icons/arow.svg') 0 0 /100% no-repeat
    &::after
      display: none
  &__arrow.swiper-button-prev
    transform: rotate(-180deg)

.slider-main-block
  &__slide.swiper-slide
    position: relative
    padding-bottom: 95%
    img
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      object-fit: cover

.content-main-block
  padding-top: 40px
  &__body
    position: relative
    z-index: 2
    padding-right: 80px
  &__sub-title
    text-transform: uppercase
    font-weight: 700
    font-size: 22px
    color: #FF9900
    margin-bottom: 20px
  &__title
    max-width: 484px
    font-weight: 800
    font-size: 54px
    line-height: 73px
    color: #000000
    margin-bottom: 20px
  &__text
    font-weight: 400
    font-size: 17px
    letter-spacing: 0.01em
    margin-bottom: 45px
  &__form
    position: relative
    z-index: 3

.form-main-block
  display: flex
  min-height: 86px
  margin-right: -189px
  background: #FFFFFF
  background: url('../img/icons/input-icon.svg') 33px 33px no-repeat
  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0227778), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0182222), 0px 20px 13px rgba(0, 0, 0, 0.015), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.0117778), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00722222)
  &__input
    width: 100%
    flex: 1 1 auto
    padding-left: 74px
    font-size: 20px
    letter-spacing: 0.01em
    border: 1px solid #DADADA
    background-color: transparent
    &::placeholder
      color: #31353B
      opacity: 0.3
  &__btn
    flex: 0 0 188px
    background: #F85A47
    font-weight: 600
    font-size: 17px
    letter-spacing: 0.01em
    color: #FFFFFF
    border: none
    cursor: pointer
    padding: 5px 15px
    &:hover
      background-color: #b9473a

.partners-main-block
  max-width: 66%
  &__label
    font-size: 17px
    letter-spacing: 0.01em
    margin-bottom: 25px
  &__items
    display: flex
    flex-wrap: wrap
    flex-direction: row
    justify-content: space-between
    align-items: center
    gap: 5px
  &__item
    img
      max-width: 100%

.how
  padding-top: 110px
  padding-bottom: 110px
  &__container,
  &__title
    margin-bottom: 70px
  &__items
    display: grid
    grid-template-columns: repeat(3, minmax(auto,260px))
    column-gap: 30px
    row-gap: 40px
    justify-content: space-between

.item-how
  text-align: center
  display: grid
  align-items: center
  justify-content: space-between
  position: relative
  &__icon
    margin: 0 auto
    display: flex
    justify-content: center
    align-items: center
    width: 90px
    height: 90px
    border-radius: 20px
    margin-bottom: 50px
  &_violet .item-how__icon
    background-color: #9672ff
    box-shadow: 0px 100px 80px rgba(150, 114, 255, 0.07), 0px 64.8148px 46.8519px rgba(150, 114, 255, 0.0531481), 0px 38.5185px 25.4815px rgba(150, 114, 255, 0.0425185), 0px 20px 13px rgba(150, 114, 255, 0.035), 0px 8.14815px 6.51852px rgba(150, 114, 255, 0.0274815), 0px 1.85185px 3.14815px rgba(150, 114, 255, 0.0168519)
  &:not(:last-child)::after
    content: ""
    position: absolute
    right: 0
    top: 41px
    transform: translate(85.5%, 0)
    width: 210px
    height: 11px
    background: url('../img/how-border_1.png') 0 0 / 100% no-repeat
  &_blue .item-how__icon
    background-color: #4DDFFD
    box-shadow: 0px 100px 80px rgba(77, 223, 253, 0.07), 0px 64.8148px 46.8519px rgba(77, 223, 253, 0.0531481), 0px 38.5185px 25.4815px rgba(77, 223, 253, 0.0425185), 0px 20px 13px rgba(77, 223, 253, 0.035), 0px 8.14815px 6.51852px rgba(77, 223, 253, 0.0274815), 0px 1.85185px 3.14815px rgba(77, 223, 253, 0.0168519)
  &_pink .item-how__icon
    background: #F2B8EC
    box-shadow: 0px 100px 80px rgba(242, 184, 236, 0.07), 0px 64.8148px 46.8519px rgba(242, 184, 236, 0.0531481), 0px 38.5185px 25.4815px rgba(242, 184, 236, 0.0425185), 0px 20px 13px rgba(242, 184, 236, 0.035), 0px 8.14815px 6.51852px rgba(242, 184, 236, 0.0274815), 0px 1.85185px 3.14815px rgba(242, 184, 236, 0.0168519)
  &__title
    font-weight: 600
    font-size: 22px
    color: #000000
    margin-bottom: 15px
  &__text
    font-weight: 400
    font-size: 16px
    color: #333333

.item-how_blue:not(:last-child)::after
  transform: translate(86.5%, 0) rotate(-180deg)

.deals
  padding-top: 110px
  padding-bottom: 110px
  &__header
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    justify-content: space-between
  &__tabs
    margin-bottom: 45px

.header-deals
  &__title
    margin-bottom: 15px
  &__text
    max-width: 470px
    font-size: 16px
    line-height: 1.5
    color: #000000
    margin-bottom: 50px
  &__btn
    min-height: 65px
    background: #F85A47
    box-shadow: 0px 100px 80px rgba(248, 90, 71, 0.07), 0px 64.8148px 46.8519px rgba(248, 90, 71, 0.0531481), 0px 38.5185px 25.4815px rgba(248, 90, 71, 0.0425185), 0px 20px 13px rgba(248, 90, 71, 0.035), 0px 8.14815px 6.51852px rgba(248, 90, 71, 0.0274815), 0px 1.85185px 3.14815px rgba(248, 90, 71, 0.0168519)
    border-radius: 5px
    font-weight: 500
    font-size: 17px
    line-height: 1.35
    color: #FFFFFF
    text-decoration: none
    padding: 22px 33px
    &:hover
      background-color: #b9473a

.tabs-deals
  &__nav
    display: flex
    column-gap: 50px
    row-gap: 10px
    flex-wrap: wrap
  &__button
    position: relative
    background-color: transparent
    transition: color 0.2s ease 0s
    font-weight: 400
    font-size: 19px
    line-height: 1.37
    color: #000000
    border: none
    cursor: pointer
    &:hover
      color: #b9473a
    &::after
      content: ""
      position: absolute
      bottom: 0
      left: 0
      width: 0
      height: 1px
      background-color: #f85a47
      transition: width 0.2s ease 0s
    &:hover::after
      width: 100%
    &.active
      color: #b9473a
      &::after
        width: 100%
  &__item
    display: none
    &.active
      display: block

.item-tabs
  &__items
    display: grid
    gap: 30px
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr))
  &__item
    position: relative
  &__options
    position: absolute
    z-index: 2
    left: 14px
    top: 14px
    display: flex
    flex-wrap: wrap
    gap: 6px
  &__option
    background-color: rgba(0, 0, 0, 0.4)
    border: 1px solid #FFFFFF
    border-radius: 5px
    padding: 6px 12px
    font-size: 12px
    line-height: 1.25
    color: #FFFFFF
    text-decoration: none
    transition: background-color 0.3s ease 0s
    &:hover
      background-color: rgba(0, 0, 0, 0.7)
  &__images
    position: relative
    display: block
    padding-bottom: 81%
    border-radius: 5px
    overflow: hidden
    img
      position: absolute
      width: 100%
      height: 100%
      top: 0
      left: 0
      object-fit: cover
      transition: transform 0.3s ease 0s
    &:hover img
      transform: scale(1.1)

.featured
  padding-top: 120px
  padding-bottom: 100px
  &__title
    margin-bottom: 95px
  &__body
    background: #f1ffff
    padding: 40px 40px 80px 40px
    display: grid
    column-gap: 77px
    row-gap: 40px
    grid-template-columns: repeat(2, 1fr)
  &__content
    display: flex
    flex-direction: column

.images-featurde
  display: grid
  justify-content: center
  &__item
    img
      max-width: 100%
      height: auto
    &_mini
      width: 100%
      max-height: 278px
      margin-left: 0px
      margin-right: 20px
      filter: drop-shadow(0px 163px 80px rgba(0, 0, 0, 0.04)) drop-shadow(0px 105.648px 46.8519px rgba(0, 0, 0, 0.0303704)) drop-shadow(0px 62.7852px 25.4815px rgba(0, 0, 0, 0.0242963)) drop-shadow(0px 32.6px 13px rgba(0, 0, 0, 0.02)) drop-shadow(0px 13.2815px 6.51852px rgba(0, 0, 0, 0.0157037)) drop-shadow(0px 3.01852px 3.14815px rgba(0, 0, 0, 0.00962963)) drop-shadow(48px 58px 44px rgba(0, 0, 0, 0.09))
      margin-top: -114px

.content-featured
  &__title
    font-weight: 600
    font-size: 31px
    line-height: 42px
    color: #000000
    margin-bottom: 28px
  &__text
    max-width: 335px
    font-size: 16px
    line-height: 1.5
    color: #545A58
    margin-bottom: 40px
  &__options
    display: grid
    grid-template-columns: repeat(3, 1fr)
    column-gap: 81px
    row-gap: 37px
    margin-bottom: 55px
  &__btn
    background: #F85A47
    box-shadow: 0px 100px 80px rgba(248, 90, 71, 0.07), 0px 64.8148px 46.8519px rgba(248, 90, 71, 0.0531481), 0px 38.5185px 25.4815px rgba(248, 90, 71, 0.0425185), 0px 20px 13px rgba(248, 90, 71, 0.035), 0px 8.14815px 6.51852px rgba(248, 90, 71, 0.0274815), 0px 1.85185px 3.14815px rgba(248, 90, 71, 0.0168519)
    border-radius: 5px
    font-weight: 500
    font-size: 18px
    line-height: 1.3
    color: #FFFFFF
    padding: 16px 38px
    text-decoration: none
    &:hover
      background: #b9473a

.options-content-featured
  &__label1
    font-weight: 700
    font-size: 17px
    line-height: 1.35
    color: #000000
    margin-bottom: 12px
  &__label2
    font-size: 17px
    line-height: 1.35
    color: #545A58

.reviews
  padding-top: 110px
  padding-bottom: 110px
  &__title
    margin-bottom: 50px
    max-width: 570px
  &__items
    display: grid
    gap: 30px
    grid-template-columns: repeat(auto-fill,minmax(290px,1fr))

.simple-rating
  position: relative
  font-size: 24px
  display: inline-block
  margin-bottom: 33px
  &::before
    content: "★★★★★"
    display: block
  &__items
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0
    display: flex
    flex-direction: row-reverse
    overflow: hidden
  &__item
    position: absolute
    width: 0%
    height: 0%
    opacity: 0
    visibility: hidden
    top: 0
    left: 0
    &:checked,
    &:checked~.simple-rating__label
      color: #FBB040
  &__label
    flex: 0 0 20%
    height: 100%
    cursor: pointer
    color: #f0dbbb
    &::before
      content: "★"
      display: block
      transition: color 0.3s ease 0s
    &:hover,
    &:hover~.simple-rating__label,
    &:checked~.simple-rating__label:hover
      color: #ffef9f

.item-reviews
  color: inherit
  padding: 50px 40px
  background: #FFFFFF
  border: 1px solid #EBEBEB
  border-radius: 10px
  transition: all 0.3s ease 0s
  &:hover
    border: 1px solid transparent
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0227778), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0182222), 0px 20px 13px rgba(0, 0, 0, 0.015), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.0117778), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00722222), 3px 44px 54px rgba(0, 0, 0, 0.08)
  &__raiting
    margin-bottom: 33px
  &__text
    line-height: 1.5
    margin-bottom: 50px

.author-item-reviews
  display: flex
  gap: 20px
  align-items: center
  &__avatar
    flex: 0 0 48px
    border-radius: 50%
    overflow: hidden
  &__name
    font-weight: 700
    line-height: 22px
  &__location
    color: #666666
    font-size: 14px
    line-height: 19px

.reviews__footer
  padding-top: 85px

.footer-reviews
  display: flex
  column-gap: 100px
  &__partners
    flex: 1 1 auto
  &__info
    flex: 0 0 310px
    line-height: 1.5
  & a
    color: #2CBDE7
    text-decoration: underline
  & a:hover
    text-decoration: none

.partners-footer-reviews
  display: flex
  flex-wrap: wrap
  gap: 20px
  align-items: center
  justify-content: space-between

.subscribe
  background: #FBFCFC
  padding-top: 162px
  &__title
    font-weight: 700
    font-size: 45px
    margin-bottom: 11px
  &__sub-title
    text-align: center
    font-weight: 700
    font-size: 23px
    line-height: 144.2%
    letter-spacing: 0.02em
    margin-bottom: 34px
  &__form
    max-width: 686px
    margin: 0 auto
    margin-bottom: 160px

.form-subscribe
  display: flex
  gap: 15px
  align-items: center
  background: #FFFFFF
  box-shadow: 0px 163px 80px rgba(0, 0, 0, 0.04), 0px 105.648px 46.8519px rgba(0, 0, 0, 0.0303704), 0px 62.7852px 25.4815px rgba(0, 0, 0, 0.0242963), 0px 32.6px 13px rgba(0, 0, 0, 0.02), 0px 13.2815px 6.51852px rgba(0, 0, 0, 0.0157037), 0px 3.01852px 3.14815px rgba(0, 0, 0, 0.00962963)
  padding: 25px 45px
  line-height: 144.2%
  letter-spacing: 0.02em
  &__content
    flex: 1 1 auto
    display: flex
    flex-direction: column
  &__label
    display: block
    margin-bottom: 10px
    font-weight: 500
    font-size: 17px
    opacity: 0.2
  &__input
    flex: 1 1 auto
    width: 100%
    min-height: 30px
    font-weight: 700
    font-size: 20px
    border: none
  &__button
    height: 72px
    background: #F85A47
    box-shadow: 0px 100px 80px rgba(248, 90, 71, 0.07), 0px 64.8148px 46.8519px rgba(248, 90, 71, 0.0531481), 0px 38.5185px 25.4815px rgba(248, 90, 71, 0.0425185), 0px 20px 13px rgba(248, 90, 71, 0.035), 0px 8.14815px 6.51852px rgba(248, 90, 71, 0.0274815), 0px 1.85185px 3.14815px rgba(248, 90, 71, 0.0168519)
    font-weight: 500
    font-size: 18px
    color: #FFFFFF
    border: none
    padding: 10px 40px
    &:hover
      background: #c54638

.footer
  &__inner
    max-width: 879px
    margin: 0 auto
  &__header
    display: grid
    padding-top: 60px
    padding-bottom: 60px
    border-top: 1px solid #DEDFE1
    grid-template-columns: auto 1fr
    row-gap: 70px
    column-gap: 20px
  &__logo
    font-weight: 800
    font-size: 23px
    line-height: 31px
    color: inherit
    text-decoration: none
  &__menu
    display: flex
    justify-content: flex-end
  &__copy
    font-weight: 700
    font-size: 12px
    line-height: 1.34
    text-align: center
    padding-bottom: 40px

.menu-footer
  &__list
    display: flex
    flex-wrap: wrap
    justify-content: center
    column-gap: 45px
    row-gap: 15px
  &__link
    font-weight: 700
    color: inherit
    font-size: 13px
    line-height: 17px
    &:hover
      text-decoration: underline

.social
  position: fixed
  top: 50%
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  gap: 25px
  right: 20px
  transform: translate(0px,-50%)

@media screen and (max-width: 1199.98px)
  [class*="_container"]
    max-width: 970px

  .main-block
    &__container
      padding-top: 100px
      padding-bottom: 30px
    &__body
      margin-bottom: 40px
  .content-main-block__body
    padding-right: 0px
  .body-main-block
    display: block
    &__content
      margin-bottom: 40px
  .form-main-block
    margin-right: 0
  .partners-main-block
    position: relative
    z-index: 2
    max-width: none

  .item-how:not(:last-child)::after
    width: 170px
    transform: translate(69%, 0)
  .item-how_blue:not(:last-child)::after
    transform: translate(69%, 0) rotate(-180deg)

  .featured
    &__body
      display: flex
      flex-direction: column-reverse
    &__content
      align-items: center
  .images-featurde
    justify-items: center
    &__item_mini
      margin-right: 0

@media (min-width: 992px)
  .how__items
    padding: 0 35px

  .content-featured__body
    flex: 1 1 auto
    padding-top: 40px
    max-width: 412px

  .content-featured__btn
    max-width: 412px

@media screen and (max-width: 991.98px)
  [class*="_container"]
    max-width: 750px

  .main-block
    padding-bottom: 70px

  .how
    padding-top: 70px
    padding-bottom: 70px
    &__items
      padding: 0 35px
  .item-how:not(:last-child)::after
    width: 120px
    transform: translate(65%, 0)
  .item-how_blue:not(:last-child)::after
    transform: translate(65%, 0) rotate(-180deg)

  .deals
    padding-top: 70px
    padding-bottom: 70px

  .featured
    padding-top: 70px
    padding-bottom: 70px
    &__body
      display: flex
      flex-direction: column-reverse
    &__title
      margin-bottom: 50px
  .content-featured__btn,
  .content-featured__body,
  .content-featured__text
    max-width: 100%
    text-align: center

  .reviews
    padding-top: 70px
    padding-bottom: 70px
  .footer-reviews
    flex-wrap: wrap
    row-gap: 30px
    &__info
      flex: 1 1 auto

  .subscribe
    padding-top: 70px
    padding-bottom: 70px
    &__title
      font-size: 35px
    &__sub-title
      font-size: 20px

  .social
    display: none

@media screen and (max-width: 767.98px)
  [class*="_container"]
    max-width: none
    padding: 0 15px

  .title
    font-size: 30px

  .header__container
    min-height: 0
    flex-direction: column
  .header__menu .menu__link
